<template>
    <v-container class="py-10" fluid>
        <v-row v-if="showReceipt" justify="center">
            <v-col cols="12" md="4">
                <div class="d-flex justify-space-between">
                    <v-btn
                        :color="getAppColors.primary"
                        class="white--text hidden-print"
                        @click="backToVirtualTerminal"
                    >
                        <v-icon left> mdi-arrow-left </v-icon>
                        Virtual Terminal
                    </v-btn>
                    <v-btn
                        :color="getAppColors.primary"
                        class="white--text hidden-print"
                        @click="printReceipt"
                    >
                        <v-icon left> mdi-printer </v-icon>
                        Print
                    </v-btn>
                </div>
                <v-card class="mt-2">
                    <v-card-title class="text-h4 justify-center">
                        Receipt
                    </v-card-title>
                    <v-divider />
                    <v-card-text class="text-h6">
                        <v-simple-table>
                            <template v-slot:default>
                                <tbody>
                                    <tr>
                                        <td
                                            class="text-h5 text-center"
                                            colspan="2"
                                        >
                                            {{
                                                transactionResponseData
                                                    .receiptSuggestions
                                                    .merchantName
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">Date:</td>
                                        <td class="text-right">
                                            {{
                                                formatDateTime(
                                                    transactionResponseData.timestamp,
                                                )
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">
                                            {{
                                                transactionResponseData.paymentType
                                            }}:
                                        </td>
                                        <td class="text-right">
                                            {{
                                                transactionResponseData.maskedPan
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">
                                            Transaction Type:
                                        </td>
                                        <td class="text-right">
                                            {{
                                                transactionResponseData.transactionType
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">AuthCode:</td>
                                        <td class="text-right">
                                            {{
                                                transactionResponseData.authCode
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">
                                            Requested Amount:
                                        </td>
                                        <td class="text-right">
                                            {{
                                                toCurrency(
                                                    transactionResponseData.requestedAmount,
                                                )
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">
                                            Authorized Amount:
                                        </td>
                                        <td class="text-right">
                                            {{
                                                toCurrency(
                                                    transactionResponseData.authorizedAmount,
                                                )
                                            }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td class="text-left">Tip Amount:</td>
                                        <td class="text-right">
                                            {{
                                                toCurrency(
                                                    transactionResponseData.tipAmount,
                                                )
                                            }}
                                        </td>
                                    </tr>
                                </tbody>
                            </template>
                        </v-simple-table>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>

        <v-row v-if="!showReceipt" justify="center" class="hidden-print">
            <v-col cols="12" md="6">
                <v-alert
                    v-model="alert"
                    :type="alertType"
                    border="left"
                    close-text="Close Alert"
                    dismissible
                    transition="scale-transition"
                >
                    {{ alertMessage }}
                </v-alert>
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark flat>
                        <v-toolbar-title> Virtual Terminal </v-toolbar-title>
                        <v-spacer />
                        <v-btn-toggle
                            v-model="editedData.transactionType"
                            color="primary"
                            active-class="v-item--active font-weight-bold"
                            tile
                            mandatory
                            light
                            dense
                        >
                            <v-btn
                                v-for="(type, index) in transactionTypes"
                                :value="type"
                                :key="index"
                            >
                                {{ type }}
                            </v-btn>
                        </v-btn-toggle>
                    </v-toolbar>
                    <v-card-text>
                        <v-form
                            ref="virtualTerminalForm"
                            id="virtualTerminalForm"
                            v-model="valid"
                            lazy-validation
                        >
                            <v-subheader
                                class="subtitle-2 font-weight-black px-0"
                            >
                                Transaction Info
                            </v-subheader>
                            <v-row>
                                <v-col cols="12" md="6" lg="3">
                                    <v-text-field
                                        v-model="editedData.amount"
                                        label="Amount"
                                        :color="getAppColors.primary"
                                        :rules="validationRules.amount"
                                        type="number"
                                        prefix="$"
                                        step="0.01"
                                        required
                                        @change="
                                            editedData.amount = formatCurrency(
                                                editedData.amount,
                                            )
                                        "
                                    />
                                </v-col>
                                <v-col cols="12" md="6" lg="3">
                                    <v-text-field
                                        v-model="editedData.tipAmount"
                                        label="Tip Amount"
                                        :color="getAppColors.primary"
                                        :rules="rules.tipAmount"
                                        type="number"
                                        prefix="$"
                                        step="0.01"
                                        @change="
                                            editedData.tipAmount =
                                                formatCurrency(
                                                    editedData.tipAmount,
                                                )
                                        "
                                    />
                                </v-col>
                                <v-col cols="12" md="6" lg="3">
                                    <v-slide-x-transition mode="out-in">
                                        <v-select
                                            v-model="editedData.terminal"
                                            :items="terminals"
                                            :rules="[
                                                (v) =>
                                                    !!v ||
                                                    'Terminal is required',
                                            ]"
                                            label="Terminal"
                                            :color="getAppColors.primary"
                                            item-color="success"
                                            required
                                            v-if="
                                                editedData.transactionType !==
                                                'Refund'
                                            "
                                        />
                                        <v-text-field
                                            v-model="editedData.transactionId"
                                            label="Transaction ID"
                                            :color="getAppColors.primary"
                                            :rules="rules.transactionId"
                                            v-else
                                        />
                                    </v-slide-x-transition>
                                </v-col>
                            </v-row>
                            <v-slide-y-transition group>
                                <template
                                    v-if="
                                        editedData.transactionType !== 'Refund'
                                    "
                                >
                                    <v-subheader
                                        class="subtitle-2 font-weight-black px-0"
                                        key="1"
                                    >
                                        Payment Info
                                    </v-subheader>
                                    <v-row key="2">
                                        <v-col cols="12" md="6" lg="4">
                                            <v-text-field
                                                v-model="
                                                    editedData.cardholderName
                                                "
                                                label="Cardholder Name"
                                                :color="getAppColors.primary"
                                                :rules="rules.cardholderName"
                                                required
                                            />
                                        </v-col>
                                        <v-col cols="12" md="6" lg="4">
                                            <v-text-field
                                                v-model="editedData.cardNumber"
                                                label="Card Number"
                                                :color="getAppColors.primary"
                                                :rules="rules.cardNumber"
                                                required
                                                v-cardformat:formatCardNumber
                                            >
                                                <template v-slot:append>
                                                    <v-icon>{{
                                                        cardIcon
                                                    }}</v-icon>
                                                </template>
                                            </v-text-field>
                                        </v-col>
                                        <v-col cols="12" md="3" lg="4">
                                            <v-text-field
                                                v-model="editedData.postalCode"
                                                label="Zip Code"
                                                :color="getAppColors.primary"
                                                :rules="rules.postalCode"
                                                required
                                            />
                                        </v-col>
                                        <v-col cols="12" md="3" lg="2">
                                            <v-autocomplete
                                                v-model="
                                                    editedData.expirationMonth
                                                "
                                                :items="months"
                                                label="Exp Month"
                                                :color="getAppColors.primary"
                                                :rules="rules.expirationMonth"
                                                item-color="success"
                                                required
                                            />
                                        </v-col>
                                        <v-col cols="12" md="3" lg="2">
                                            <v-autocomplete
                                                v-model="
                                                    editedData.expirationYear
                                                "
                                                :items="years"
                                                label="Exp Year"
                                                :color="getAppColors.primary"
                                                :rules="rules.expirationYear"
                                                item-color="success"
                                                required
                                            />
                                        </v-col>
                                        <v-col cols="12" md="3" lg="2">
                                            <v-text-field
                                                v-model="editedData.cvv"
                                                label="CVV"
                                                :color="getAppColors.primary"
                                                :rules="rules.cvv"
                                                type="number"
                                                required
                                                hide-spin-buttons
                                            />
                                        </v-col>
                                    </v-row>
                                </template>
                            </v-slide-y-transition>
                            <v-subheader
                                class="subtitle-2 font-weight-black px-0"
                            >
                                Optional
                            </v-subheader>
                            <v-row>
                                <v-col cols="12" lg="4">
                                    <v-text-field
                                        v-model="editedData.customerEmail"
                                        label="Customer Email"
                                        :color="getAppColors.primary"
                                        :rules="rules.customerEmail"
                                        type="email"
                                        hint="If provided, the customer will be emailed a receipt"
                                        persistent-hint
                                    />
                                </v-col>
                                <v-col cols="12" lg="4">
                                    <v-text-field
                                        v-model="editedData.description"
                                        label="Description"
                                        :color="getAppColors.primary"
                                        hint="e.g. 'Payment for Invoice 1942'"
                                        persistent-hint
                                    />
                                </v-col>
                                <v-col cols="12" lg="4">
                                    <v-text-field
                                        v-model="
                                            editedData.transactionReference
                                        "
                                        label="Reference ID"
                                        :color="getAppColors.primary"
                                    />
                                </v-col>
                            </v-row>
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn color="error" @click="reset"> Cancel </v-btn>
                        <v-spacer />
                        <v-btn
                            :disabled="!valid"
                            color="success"
                            @click="submit"
                        >
                            Submit
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import accounting from 'accounting';
import axios from 'axios';
import moment from 'moment';
import { mapGetters } from 'vuex';

export default {
    name: 'VirtualTerminalScreen',
    data: () => ({
        loading: false,
        valid: false,
        alert: false,
        alertMessage: '',
        alertType: 'success',
        transactionTypes: ['Charge', 'Preauth', 'Refund'],
        rules: {
            charge: {
                amount: [(v) => v > 0 || 'Amount must be positive'],
                tipAmount: [(v) => v >= 0 || 'Tip amount cannot be negative'],
                cardNumber: [(v) => !!v || 'Card number is required'],
                expirationMonth: [(v) => !!v || 'Exp Month is required'],
                expirationYear: [(v) => !!v || 'Exp Year is required'],
                cvv: [(v) => !!v || 'CVV is required'],
                postalCode: [
                    (v) =>
                        /(^\d{5}$)|(^\d{5}-\d{4}$)/.test(v) ||
                        'Postal code must be valid',
                ],
                cardholderName: [(v) => !!v || 'Cardholder name is required'],
                customerEmail: [
                    (v) =>
                        (v ? /.+@.+\..+/.test(v) : true) ||
                        'E-mail must be valid',
                ],
            },
            preauth: {},
            refund: {},
        },
        editedData: {
            amount: null,
            tipAmount: null,
            terminal: 'Manual',
            transactionType: 'Charge',
            cardNumber: null,
            expirationMonth: null,
            expirationYear: null,
            cvv: null,
            postalCode: null,
            cardholderName: null,
            transactionReference: null,
            description: null,
            customerEmail: null,
            transactionId: null,
        },
        defaultData: {
            amount: null,
            tipAmount: null,
            terminal: 'Manual',
            transactionType: 'Charge',
            cardNumber: null,
            expirationMonth: null,
            expirationYear: null,
            cvv: null,
            postalCode: null,
            cardholderName: null,
            transactionReference: null,
            description: null,
            customerEmail: null,
            transactionId: null,
        },
        currentYear: new Date().getFullYear(),
        cardBrand: null,
        showReceipt: false,
        transactionResponseData: null,
    }),
    computed: {
        ...mapGetters(['getAppColors', 'getInternalCoreClearController']),
        terminals() {
            return ['Manual'];
        },
        months() {
            return [
                '01',
                '02',
                '03',
                '04',
                '05',
                '06',
                '07',
                '08',
                '09',
                '10',
                '11',
                '12',
            ];
        },
        years() {
            const years = [];

            for (let i = 0; i < 12; i++) {
                years.push((this.currentYear + i).toString());
            }

            return years;
        },
        cardIcon() {
            return this.getCardIcon(this.cardBrand);
        },
        validationRules() {
            let rules;

            switch (this.editedData.transactionType) {
                case 'Charge':
                    rules = this.rules.charge;
                    break;
                case 'Preauth':
                    rules = this.rules.preauth;
                    break;
                case 'Refund':
                    rules = this.rules.refund;
                    break;
                default:
                    rules = this.rules.charge;
            }

            return rules;
        },
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        submit() {
            if (this.$refs.virtualTerminalForm.validate()) {
                this.loading = true;
                this.alert = false;
                this.attempt_transaction();
            }
        },
        reset() {
            this.editedData = Object.assign({}, this.defaultData);
        },
        async attempt_transaction() {
            // These form fields MUST match what we have defined in coreSTORE application/controllers/coreclear.php
            let formData = new FormData();
            formData.append('amount', this.editedData.amount);
            formData.append('tipAmount', this.editedData.tipAmount);
            formData.append('terminal', this.editedData.terminal);
            formData.append('transactionType', this.editedData.transactionType);
            formData.append(
                'cardNumber',
                this.editedData.cardNumber
                    ? this.editedData.cardNumber.replace(/\s/g, '')
                    : null,
            );
            formData.append('expirationMonth', this.editedData.expirationMonth);
            formData.append('expirationYear', this.editedData.expirationYear);
            formData.append('cvv', this.editedData.cvv);
            formData.append('postalCode', this.editedData.postalCode);
            formData.append('cardholderName', this.editedData.cardholderName);
            formData.append(
                'transactionReference',
                this.editedData.transactionReference,
            );
            formData.append('description', this.editedData.description);
            formData.append('email', this.editedData.customerEmail);
            formData.append('transactionId', this.editedData.transactionId);

            try {
                let response = await axios.post(
                    `${
                        this.getInternalCoreClearController +
                        '/virtual_terminal_attempt_transaction/' +
                        this.editedData.transactionType
                    }`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                if (response.data.success === true) {
                    let alertType = 'error';
                    let alertMessage = 'Unknown error';

                    if (response.data.approved === true) {
                        this.showReceipt = true;
                        this.transactionResponseData = response.data;

                        alertType = 'success';
                        alertMessage =
                            'Approved: ' +
                            this.editedData.transactionType +
                            ' was completed successfully';

                        if (
                            response.data.authorizedAmount !=
                            response.data.requestedAmount
                        ) {
                            alertType = 'warning';
                            alertMessage =
                                'Approved: Authorized amount was less than you requested. Requested: ' +
                                response.data.requestedAmount +
                                ' Authorized: ' +
                                response.data.authorizedAmount;
                        }

                        this.reset();
                    }

                    this.openAlert(alertType, alertMessage);
                } else {
                    if (response.data.responseDescription) {
                        if (
                            response.data.responseDescription ==
                            'CHECK DIGIT ERR'
                        ) {
                            this.openAlert(
                                'error',
                                'CHECK DIGIT ERR: Transaction failed. Please check the card number and try again.',
                            );
                        } else {
                            this.openAlert(
                                'error',
                                response.data.responseDescription,
                            );
                        }
                    } else {
                        this.openAlert(
                            'error',
                            'The transaction failed with an unknown error. Please contact support.',
                        );
                    }
                }
            } catch (error) {
                console.error('Could not complete transaction');
                console.error(error);

                this.openAlert('error', error);
            }
        },
        formatCurrency(rawAmount) {
            if (!rawAmount) return '0';
            return accounting.formatNumber(rawAmount, 2, '', '.');
        },
        getCardIcon: function (cardType) {
            cardType = cardType || 'unknown';
            let icon = '';
            let cardTypeIcons = {
                visa: 'fab fa-cc-visa',
                mastercard: 'fab fa-cc-mastercard',
                amex: 'fab fa-cc-amex',
                discover: 'fab fa-cc-discover',
                diners: 'fab fa-cc-diners-club',
                jcb: 'fab fa-cc-jcb',
                unknown: 'fa fa-credit-card',
            };

            icon = cardTypeIcons[cardType]
                ? cardTypeIcons[cardType]
                : 'fa fa-credit-card';

            return icon;
        },
        toCurrency: (value) => {
            if (!value) return '0';
            return accounting.formatMoney(value);
        },
        formatDateTime(rawDateTime) {
            return rawDateTime
                ? moment(rawDateTime).format('MM/DD/YYYY LT')
                : '';
        },
        printReceipt() {
            window.print();
        },
        backToVirtualTerminal() {
            this.showReceipt = false;
        },
    },
};
</script>

<style scoped>
td {
    border: 0 !important;
}
</style>
